import './Dashboard.scss';
import React, { useMemo, useContext } from 'react';
import { useTable, usePagination } from 'react-table';
import JobPostContext from '../context/job-post-context';

const DashboardContext = () => {
  const { jobState } = useContext(JobPostContext);

  const Columns = [
    {
      Header: '',
      accessor: 'title',
    },
    {
      Header: '',
      accessor: 'companyName',
    },
    {
      Header: '',
      accessor: r => {
        if (r.foundingDate) {
          return r.foundingDate + '';
        }
      },
    },
    {
      Header: '',
      accessor: r => {
        if (r.revenue) {
          return r.revenue / 100000000 + '';
        }
      },
    },
    {
      Header: '',
      accessor: r => {
        if (r.employee) {
          return r.employee + '';
        }
      },
    },
  ];

  const columns = useMemo(() => Columns, []);

  const tableInstance = useTable(
    {
      columns: columns,
      data: jobState.posts,
      initialState: { pageIndex: 0, pageSize: 17 },
    },
    usePagination
  );

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    page,
    nextPage,
    previousPage,
    prepareRow,
    canPreviousPage,
    canNextPage,
    pageOptions,
    state,
    gotoPage,
    pageCount,
  } = tableInstance;

  const { pageIndex } = state;

  return (
    <div className="dashboard">
      {jobState.posts.length ? (
        <>
          <table {...getTableProps()}>
            <thead>
              {headerGroups.map(headerGroup => (
                <tr {...headerGroup.getHeaderGroupProps()}>
                  {headerGroup.headers.map(column => (
                    <th {...column.getHeaderProps()}>
                      {column.render('Header')}
                    </th>
                  ))}
                </tr>
              ))}
            </thead>
            <tbody {...getTableBodyProps()}>
              {page.map((row, rowIndex) => {
                prepareRow(row);
                return (
                  <tr {...row.getRowProps()}>
                    {row.cells.map((cell, cellIndex) => {
                      return (
                        <td {...cell.getCellProps()}>
                          {cellIndex === 0 ? (
                            <a
                              href={jobState.posts[rowIndex].url}
                              target="_blank"
                              rel="noreferrer"
                            >
                              {cell.render('Cell')}
                            </a>
                          ) : (
                            cell.render('Cell')
                          )}
                        </td>
                      );
                    })}
                  </tr>
                );
              })}
            </tbody>
          </table>
          <div class="pagination">
            <span>
              {pageIndex + 1} of {pageOptions.length}
            </span>
            <button onClick={() => gotoPage(0)} disabled={!canPreviousPage}>
              {'<<'}
            </button>
            <button onClick={() => previousPage()} disabled={!canPreviousPage}>
              {'<'}
            </button>
            <button onClick={() => nextPage()} disabled={!canNextPage}>
              {'>'}
            </button>
            <button
              onClick={() => gotoPage(pageCount - 1)}
              disabled={!canNextPage}
            >
              {'>>'}
            </button>
          </div>
        </>
      ) : (
        ''
      )}
    </div>
  );
};

export default DashboardContext;
